@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Raleway:ital,wght@0,300;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&family=Roboto:wght@100;300;400;700&family=Ubuntu:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500;1,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    /* font-family: 'Ubuntu', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-family: 'Mulish', sans-serif; */
}

body {
    min-height: 100vh;
}

.header {
    /* border: 2px solid red; */
    display: flex;
    height: 75px;
    align-items: center;
    padding-left: 32px;
    background-color: #232219;
}

.header h1 {
    font-size: 26px;
    color: #fff;
}

footer p {
    position: fixed;
    bottom: 5px;
    text-align: center;
    width: 100%;
}

.main_note {
    width: 450px;
    margin: 20px auto;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    position: relative;
}

.form {
    display: flex;
    flex-direction: column;
}

.form input {
    font-size: 25px;
    font-weight: bold;
    border: none;
    padding: 5px;
    outline: none;
}

.form textarea {
    margin-top: 10px;
    font-size: 18px;
    font-weight: 500;
    border: none;
    padding: 5px;
    outline: none;
    min-height: 130px;
    max-width: 450px;
}


.plus_sign {
    position: absolute !important;
    right: -5px;
    bottom: -10px;
    border-radius: 50px;
    font-size: 45px !important;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    background-color: #fff;
    color: #dac503;
}

.plus_sign:hover{
    background-color: #dac503;
    color: #fff;
    cursor: pointer;
}

.note {
    width: 300px;
    height: 150px;
    padding: 12px;
    position: relative;
    margin-left: 12px;
    background-color: beige;
}

.delete_icon{
    position: absolute;
    right: 10px;
    bottom: 10px;
    color: #2f2c14;
}

.delete_icon:hover{
    border-radius: 50px;
    cursor: pointer;
    color: #ffffff;
    background-color: #cd561a;
}

.note_container{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;    
    display: inline-flex;
    justify-content: space-evenly;
    margin: 12px;
    flex-wrap: wrap;
}